import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/book/:slug',
    name: 'BookPage',
    props: true,
    component: () => import(/* webpackChunkName: "BookPage" */ '../views/BookPage.vue')
  },
  // {
  //   path: '/LoadingScreen',
  //   name: 'LoadingScreen',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "BookPage" */ '../views/LoadingScreen.vue')
  // }
]

const router = new VueRouter({
  mode: "history",
  routes
})

export default router
